.blog {
  background-color: $background-color-light;

  &_context {
    background-color: $use-accent-color;
    @media screen and (min-width: $tablet) {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}

.content_blog {
  color: $main-color-white;
  padding: 30px 30px 30px 30px;

  @media screen and (min-width: $tablet) {
    width: 50%;
    padding-right: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
  }
  // @media screen and (min-width: $desktop) {
  //   padding: 112px 80px 113px 30px;
  // }
  .section_title {
    color: $main-color-white;
    text-align: start;
    margin-bottom: 25px;
    @media screen and (min-width: $tablet) {
      margin-bottom: 26px;
    }
  }
  .content_text {
    margin-bottom: 14px;
    @media screen and (min-width: $tablet) {
      margin-bottom: 20px;
    }
    @media screen and (min-width: $desktop) {
      margin-bottom: 17px;
    }
  }
}
